.cards {
    background: #ffffff;
  }
  
  h1 {
    text-align: center;
    font-size: 3rem;
    margin-top: -5px;
    font-family: 'Playfair Display Regular 400 Italic', serif;
    font-weight: lighter;

  }
  .cards__item__img {
    /* position: absolute; */
    margin-top: 55px;
    margin-left: 24px;
    right: 0;
    bottom: 0;
    left: 20%;
    width: 256px;
    height: 400px;
    object-fit: cover;
    transition: all 0.2s linear;
    
  }

  .cards__item__inner{
    width: 600px;
    display: flex;
    /* margin: auto; */
}

  .cards__item__text {
    color: #252e48;
    font-size: 12px;
    line-height: 17px;
    margin-left: 17px;
    margin-right: 27px;
    text-align: center;
    margin-top: 8px;
  }

  .rows {
    display: flex;
    margin: auto;
    width: max-content;
  }

  .not-rows {
    display: flex;
    margin: auto;
    width: 1200px;
}

  .book__title {
    color: #252e48;
    font-size: 27px;
    line-height: 10px;
    margin-left: 17px;
    margin-right: 27px;
    text-align: center;
    margin-top: 60px;
  }

   /* Social Icons */
   .social-icon-link {
    color: black;
    font-size: 15px;
  }
  
  .social-media {
    max-width: 1000px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 50px;
    margin: 00px auto 0 auto;
    margin: auto;
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    text-align: center;
    width: 100px;
    margin-left: 18px;
    margin-top: 10px;
  }
  
  .social-logo {
    color: black;
    /* justify-self: start; */
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  
  @media only screen and (max-width: 960px) {
    .content__blog__container {
      width: 84%;
    }
    .rows {
        display: inline;
        margin: auto;
      }
  }

  @media only screen and (max-width: 160px) {
    .rows {
        display: inline;
        margin: auto;
        width: 100px;
      }
  }
